import React from "react";
import Recharge from "../../container/Recharge";
import "./MyOrder.less";
import {toggleSlide} from "../Withdraw_cash/config";
const api="http://111.231.57.144:8081/api"
function timestampToTime(timestamp) {
    var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
    // var Y = date.getFullYear() + '-';
    var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '.';
    var D = (date.getDate()<10 ? '0'+date.getDate():date.getDate()) + ' ';
    var h = (date.getHours()<10 ? '0'+ date.getHours():date.getHours())+ ':';
    var m = (date.getMinutes()<10 ? '0' +date.getMinutes():date.getMinutes())  + '';
    // var s = (date.getSeconds()<10 ? '0'+date.getSeconds() :date.getSeconds());
    return M+D+h+m;
}
export default class MyOrder extends React.Component{
    constructor(props){
        super(props);
        this.state={
            page:1,
            "isShow":false,
            recharge_id:"",
            order_id:'',
            order_status:0,
            //输入上分金额
            input_money:'',
            shangfenId:"",
        }
        fetch(`${api}/order/orderList?replace_id=${this.props.data.user_id}&user_id=${this.state.recharge_id}&order_status=${this.state.order_status}&page=${this.state.page}&page_set=5`,{
            method:"GET"
        }).then((data)=>data.json())
        .then((responseJson)=>{
            // eslint-disable-next-line
            if(responseJson.status==0){
                this.setState({
                    results:responseJson,
                    myOrderList:responseJson.data.list
                })
            }else{
                this.setState({
                    myOrderList:[]
                })
            }
           
        })
        .catch((error) =>{
            alert(error)
        })
    }
    setPath(val){
        this.props.setProps(val)
    }
    showAlert(value){
        this.setState({
            isShow:true
        })
    }
    hideAlert(){
        this.setState({
            isShow:false
        })
    }
    //避免异步请求结果未返回就切换页面报错
    componentWillUnmount(){
        this.setState = (state,callback)=>{
          return;
        };
    }
    render(){
        const{user_id} =this.props.data;
        var {results,myOrderList}=this.state;
        if(results){
            // eslint-disable-next-line 
            var totalPage = results.data.total_page ==0?1:results.data.total_page;
        }else{
            // eslint-disable-next-line 
            var totalPage = 1;
        }
        const height =this.props.height;
        var dataList =[
            "全部",
            "未完成",
            "已完成"
        ]
        return  <Recharge height={height} current="1" setPath={this.setPath.bind(this)}>
            <div className="MyOrdertitle">
                <div onTouchEnd={()=>{
                        this.props.setProps("#/")
                    }}
                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/fanhui1.png)"}} 
                ></div>
                <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dalingdang.png)"}} 
                    onTouchEnd={()=>{
                        this.props.setProps("#/recharge/MyDc")
                    }}
                ><span>我的代充</span></div>
                <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dalingdang.png)"}} 
                ><span>我的订单</span></div>
            </div>
            <div className="MyOrderBgWhite" >
                <div className="MyOrderBgWhite_child1">
                    <div className="MyOrderBgWhite_child1_child1 publicinput">
                        <input type="text" placeholder="输入ID"
                            maxLength="10"
                            onInput={(e)=>{
                                e.target.value= e.target.value.match(/\d+(\.\d{0,2})?/) ? e.target.value.match(/\d+(\.\d{0,2})?/)[0] : ''
                            }}
                            onChange={(e)=>{
                                this.setState({
                                    recharge_id:e.target.value
                                })
                            }}
                            value={this.state.recharge_id}
                        />
                         <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)" }} 
                            onClick={()=>{
                                this.setState({
                                    recharge_id :''
                                })
                            }} 
                        ></i>
                    </div>
                    <div  className="MyOrderBgWhite_child1_select publicinput">
                                <form action="" method="post" > 
                                    <div id="divselect" > 
                                        <cite id="cite"
                                               onClick={()=>{
                                                toggleSlide('selectUl','500','80')
                                            }}
                                        >全部</cite> 
                                        <ul id="selectUl" style={{display:"none"}} > 
                                        {
                                            dataList.map((item,index)=>{
                                                return  <li key={index}
                                                    onClick={()=>{
                                                        document.getElementById("selectUl").style.display="none"
                                                        document.getElementById("cite").innerHTML=item;
                                                        this.setState({
                                                            order_status:index
                                                        })
                                                    }}
                                                    // eslint-disable-next-line 
                                                ><a href="javascript:;" selectid={index+1}>{item}</a></li>
                                            })
                                        }
                                        </ul> 
                                        
                                    </div> 
                                    <input name="" type="hidden" value="" id="inputselect"/> 
                                </form> 
                                <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/1.png)"}}></i>
                            </div>
                    <div
                        className="MyOrderBgWhite_child1_child3 publicbtn"
                        style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                        onClick={()=>{
                            this.setState({
                                page:1
                            })
                            fetch(`${api}/order/orderList?replace_id=${this.props.data.user_id}&user_id=${this.state.recharge_id}&order_status=${this.state.order_status}&page=1&page_set=5`,{
                                method:"GET"
                            }).then((data)=>data.json())
                            .then((responseJson)=>{
                                this.setState({
                                    results:responseJson,
                                    myOrderList:responseJson.data.list
                                })
                            })
                            .catch((error) =>{
                                alert(error)
                            })
                        }}
                    > <span>搜 索</span></div>
                </div>
                <div className="MyOrderBgWhite_child2">
                    <span className="WordArt">订单号</span>
                    <span className="WordArt">昵称</span>
                    <span className="WordArt">状态</span>
                    <span className="WordArt">应上金额</span>
                    <span className="WordArt">实上金额</span>
                    <span className="WordArt">创建时间</span>
                </div>
                {/* 动态展示列表 */}
                <div 
                    className="MyOrderBgWhite_child3"
                    // style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/gezi5.png)"}}
                    >
                    {
                        // eslint-disable-next-line
                        myOrderList==undefined?"":myOrderList.map((item,index)=>{
                            return <div className="orderlist" key={index}
                                    >
                                        <div>{item.user_id}</div>
                                        <div>{item.nick_name}</div>
                                        {/* eslint-disable-next-line  */}
                                        <div>{item.status==0 ? "支付中":(item.status==1?"已完成":(item.status==2?"已过期":"已撤销"))}</div>
                                        <div>{parseInt(item.amount)}</div>
                                        <div>{parseInt(item.actual_amount)}</div>
                                        <div>{timestampToTime(item.created_at)}</div>
                                        {
                                        //  eslint-disable-next-line  
                                           item.status==0 ?  <div id="shangfenBtn"
                                            className='shangfenBtn'
                                            onClick={()=>{
                                                this.showAlert()
                                                this.setState({
                                                    shangfenId:item.user_id,
                                                    order_id:item.order_id,
                                                    nick_name:item.nick_name,
                                                    amount:item.amount
                                                })
                                            }} 
                                            >
                                                充值
                                            </div>
                                            :null
                                        }
                                </div>
                                
                        })
                    }
                </div>
                <div className="MyOrderBgWhite_child4">
                    {/* 上一页 */}
                    <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                        className="publicbtn"
                           onClick={()=>{
                            if(this.state.page >1){
                                this.setState({
                                    page:this.state.page-1
                                })
                                fetch(`${api}/order/orderList?replace_id=${this.props.data.user_id}&user_id=${this.state.recharge_id}&order_status=${this.state.order_status}&page=${this.state.page-1}&page_set=5`,{
                                    method:"GET"
                                }).then((data)=>data.json())
                                .then((responseJson)=>{
                                    this.setState({
                                        results:responseJson,
                                        myOrderList:responseJson.data.list
                                    })
                                })
                                .catch((error) =>{
                                    alert(error)
                                })
                            }else{
                                return null
                            }
                        }}
                    >上一页</div>
                    {/* eslint-disable-next-line  */}
                    <div>{this.state.page}/{totalPage==0?1:totalPage}</div>
                    {/* 下一页 */}
                    <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                        className="publicbtn"
                        onClick={()=>{
                            if(this.state.page<totalPage){
                                this.setState({
                                    page:this.state.page+1
                                })
                                fetch(`${api}/order/orderList?replace_id=${this.props.data.user_id}&user_id=${this.state.recharge_id}&order_status=${this.state.order_status}&page=${this.state.page+1}&page_set=5`,{
                                    method:"GET"
                                }).then((data)=>data.json())
                                .then((responseJson)=>{
                                    this.setState({
                                        results:responseJson,
                                        myOrderList:responseJson.data.list
                                    })
                                })
                                .catch((error) =>{
                                    alert(error)
                                })
                            }else{
                                return null
                            }
                        }}
                    >下一页</div>
                </div>
            </div>  
                  {/* 上分弹窗 */}
            {
                !this.state.isShow?null : <div className="div-container" id="div-container"  style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}>
                <div className="div-child-container" >
                    <div className="div-title" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/toutpd.png)"}}>
                            <div  className="div-title-child1" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/daichongd.png)"}}>
                                <span>代充上分</span>
                            </div>
                        </div>
                        <div className="div-icon" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/denglng.png)"}}
                                onTouchEnd={()=>{
                                    this.setState({
                                        isShow:false,
                                        input_money:''
                                    })
                                }}
                        ></div>
                    <div className="div-foot" style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/tou.png)" }}></div>
                    <div className="MyOrder-child">
                            <div><span>玩家昵称:</span><span>{this.state.nick_name}</span></div>
                            <div><span>玩家ID:</span><span>{this.state.shangfenId}</span></div>
                            <div><span>订单金额:</span><span>{Math.floor(this.state.amount*100)/100}</span></div>
                            <div>
                                <span>充值金额:</span>
                                 <span className='publicinput'>
                                     <input type="text" 
                                        maxLength="7"
                                        onChange={(e)=>{
                                            this.setState({
                                                input_money:e.target.value
                                            })
                                        }}
                                        onInput={(e)=>{
                                            e.target.value= e.target.value.match(/\d+(\.\d{0,2})?/) ? e.target.value.match(/\d+(\.\d{0,2})?/)[0] : ''
                                        }}
                                        value={this.state.input_money}
                                    />
                                    <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)" }} 
                                        onClick={()=>{
                                            this.setState({
                                                input_money :''
                                            })
                                        }} 
                                    ></i>
                                </span>
                            </div>
                    </div>
                    <div className="div-btn">
                            <div className="publicbtn"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)",marginLeft:'35%'}}
                                onClick={()=>{
                                    // eslint-disable-next-line
                                    if(this.state.input_money==''){
                                        this.setState({
                                            isAlert:true,
                                            msg:"金额不能为空！"
                                        })
                                    }else{
                                        this.setState({
                                            isAlert2:true,
                                            msg:`您确认给ID为${this.state.shangfenId}的玩家充值${this.state.input_money}金币吗`
                                        })
                                    }
                                    
                                }}
                            >确认
                            </div>
                    </div>
                </div>
            </div>  
            }
            {/* 上分结果弹窗 */}
             {
                    !this.state.isAlert?null:<div className="Alert-container" id="Alert-container"   style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}>
                    <div className="Alert-child-container" >
                        <div className="Alert-child ">
                        {/*  eslint-disable-next-line  */}
                            {this.state.msg}     
                        </div>
                        <div className="Alert-btn">
                                <div
                                    onClick={()=>{
                                        //重新拉取数据
                                        fetch(`${api}/order/orderList?replace_id=${this.props.data.user_id}&user_id=${this.state.recharge_id}&order_status=${this.state.order_status}&page=${this.state.page}&page_set=5`,{
                                            method:"GET"
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            this.setState({
                                                results:responseJson,
                                                myOrderList:responseJson.data.list,
                                                isAlert:false,
                                                input_money:''
                                            })
                                        })
                                        .catch((error) =>{
                                            alert(error)
                                        })
                                        
                                    }}
                                >
                                    确定
                                </div>
                            </div>
                        </div>
                    </div>
                }
                {/* 上分请求弹窗 */}
             {
                    !this.state.isAlert2?null:<div className="Alert-container" id="Alert-container"   style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}>
                    <div className="Alert-child-container"  style={{height:'20%',top:'45%'}}>
                        <div className="Alert-child" style={{position:'absolute',top:'0',width:'90%',height:"65%"}}>
                        {/*  eslint-disable-next-line  */}
                            {this.state.msg}     
                        </div>
                        <div className="Alert-btn" style={{position:'absolute',bottom:'5%',left:'10%',width:'80%',height:"30%"}}>
                                <div style={{width:'40%',height:"100%",float:'left',background:'gray'}} onClick={()=>{
                                    this.setState({
                                        isAlert2:false
                                    })
                                }}>取消</div>
                                <div
                                    style={{width:'40%',height:"100%",float:'left', background: 'rgba(0, 152, 255, 255)',color:'white',display:'flex',alignItems:'center',justifyContent:'center',marginLeft:'20%'}}
                                    onClick={()=>{
                                        //发送上分请求
                                        fetch(`${api}/replace_payment/addScore?user_id=${this.state.shangfenId}&replace_id=${user_id}&amount=${this.state.input_money}&order_id=${this.state.order_id}`,{
                                            method:"GET"
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            // eslint-disable-next-line
                                            if(responseJson.status==0){
                                                this.setState({
                                                    upScorestatus:responseJson,
                                                    isAlert:true,
                                                    msg:"上分成功!"
                                                })
                                            }else{
                                                this.setState({
                                                    upScorestatus:responseJson,
                                                    isAlert:true,
                                                    msg:responseJson.msg
                                                })
                                            }
                                            this.hideAlert();
                                            this.setState({
                                                isAlert2:false
                                            })
                                        })
                                        .catch((error) =>{
                                            alert(error)
                                        })
                                    }}
                                >
                                    确定
                                </div>
                            </div>
                        </div>
                    </div>
                }
        </Recharge>
        
    }
}
